// hooks/useGoogleForm.ts
import { useState } from 'react';

const GOOGLE_FORM_ID = '1FAIpQLSdoSJv57tLdEFGNhvJKIvQe6vlIRknTGqCXnT21hjU8H1dSDg';
const FIELD_IDS = {
  name: '466204223',
  phone: '32405555',
  email: '2122071872',
  message: '176904452',
  pagePath: '1591426887',
  campaign: '688569624',
};

type BaseForm = {
  name: string;
  email: string;
  phone: string;
  message: string;
};

export const useGoogleForm = (campaignDefault = 'legacy-exteriors-lead') => {
  const [formData, setFormData] = useState<BaseForm>({
    name: '',
    email: '',
    phone: '',
    message: '',
  });
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [submitSuccess, setSubmitSuccess] = useState(false);

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target as { name: keyof BaseForm; value: string };
    setFormData(prev => ({ ...prev, [name]: value }));
  };

  /**
   * ✅ Build payload from current state + explicit overrides (no last-second state writes)
   */
  const submit = async (overrides?: Partial<BaseForm> & { campaign?: string; pagePath?: string }) => {
    setIsSubmitting(true);

    const payload: Required<BaseForm> & { campaign: string; pagePath: string } = {
      name: overrides?.name ?? formData.name,
      email: overrides?.email ?? formData.email,
      phone: overrides?.phone ?? formData.phone,
      message: overrides?.message ?? formData.message,
      campaign: overrides?.campaign ?? campaignDefault,
      pagePath: overrides?.pagePath ?? (typeof window !== 'undefined' ? window.location.pathname : '/'),
    };

    const formUrl = `https://docs.google.com/forms/d/e/${GOOGLE_FORM_ID}/formResponse`;
    const formPayload = new URLSearchParams();
    formPayload.append(`entry.${FIELD_IDS.name}`, payload.name);
    formPayload.append(`entry.${FIELD_IDS.phone}`, payload.phone);
    formPayload.append(`entry.${FIELD_IDS.email}`, payload.email);
    formPayload.append(`entry.${FIELD_IDS.message}`, payload.message);
    formPayload.append(`entry.${FIELD_IDS.pagePath}`, payload.pagePath);
    formPayload.append(`entry.${FIELD_IDS.campaign}`, payload.campaign);

    try {
      await fetch(formUrl, {
        method: 'POST',
        mode: 'no-cors',
        headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
        body: formPayload.toString(),
      });

      setSubmitSuccess(true);
      setFormData({ name: '', email: '', phone: '', message: '' });
      setTimeout(() => setSubmitSuccess(false), 5000);
    } catch (err) {
      console.error('Error submitting form', err);
    } finally {
      setIsSubmitting(false);
    }
  };

  return { formData, isSubmitting, submitSuccess, handleChange, submit };
};
