import React from 'react';
import {
  FaStar,
  FaPhone,
  FaEnvelope,
  FaMapMarkerAlt,
  FaPaperPlane,
  FaCheckCircle
} from 'react-icons/fa';
import './contact.css';
import { useGoogleForm } from '../../hooks/useGoogleForm';

const ContactPage: React.FC = () => {
  const { formData, isSubmitting, submitSuccess, handleChange, submit } =
    useGoogleForm('legacy-exteriors-lead');

  const [extraFields, setExtraFields] = React.useState({
    firstName: '',
    lastName: '',
    service: ''
  });

  const handleExtraChange = (
    e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>
  ) => {
    const { name, value } = e.target;
    setExtraFields(prev => ({ ...prev, [name]: value }));
  };

  const handleFullSubmit = (e: React.FormEvent) => {
    e.preventDefault();

    const combinedName = `${extraFields.firstName.trim()} ${extraFields.lastName.trim()}`.trim();

    const combinedMessage = [
      extraFields.service ? `Service Interested In: ${extraFields.service}` : null,
      formData.message?.trim() ? formData.message.trim() : null
    ]
      .filter(Boolean)
      .join('\n\n');

    submit({
      name: combinedName,
      message: combinedMessage,
      campaign: 'legacy-exteriors-lead'
    });
  };

  return (
    <div className="contact-page">
      <div className="contact-page__container">
        {/* Page Header */}
        <div className="contact-page__header">
          <div className="contact-page__badge">
            <FaStar className="contact-page__badge-icon" />
            Get In Touch
          </div>
          <h1 className="contact-page__title">
            Let's Start Your
            <span className="contact-page__title-accent"> Project Together</span>
          </h1>
          <p className="contact-page__subtitle">
            Ready to transform your home's exterior? Contact us today for a free consultation 
            and discover why Idaho homeowners trust Bullseye Exteriors for their roofing and siding needs.
          </p>
        </div>

        <div className="contact-page__content">
          {/* Contact Info */}
          <div className="contact-info">
            <div className="contact-info__header">
              <h2 className="contact-info__title">Contact Information</h2>
              <p className="contact-info__description">
                Get in touch with our team. We're here to answer your questions and provide 
                expert guidance for your exterior project.
              </p>
            </div>

            <div className="contact-methods">
              <div className="contact-method">
                <div className="contact-method__icon"><FaPhone /></div>
                <div className="contact-method__content">
                  <h3 className="contact-method__title">Call Us</h3>
                  <p className="contact-method__info">(208) 989-8319</p>
                  <p className="contact-method__hours">Mon-Fri: 8 AM – 6 PM</p>
                </div>
              </div>

              <div className="contact-method">
                <div className="contact-method__icon"><FaEnvelope /></div>
                <div className="contact-method__content">
                  <h3 className="contact-method__title">Email Us</h3>
                  <p className="contact-method__info">LegacyExterior@gmail.com</p>
                  <p className="contact-method__hours">Response within 24 hours</p>
                </div>
              </div>

              <div className="contact-method">
                <div className="contact-method__icon"><FaMapMarkerAlt /></div>
                <div className="contact-method__content">
                  <h3 className="contact-method__title">Visit Us</h3>
                  <p className="contact-method__info">
                    123 Construction Way<br />Boise, ID 83701
                  </p>
                  <p className="contact-method__hours">By appointment only</p>
                </div>
              </div>
            </div>
          </div>

          {/* Contact Form */}
          <div className="contact-form">
            <div className="contact-form__header">
              <h2 className="contact-form__title">Send Us a Message</h2>
              <p className="contact-form__description">
                Fill out the form below and we'll get back to you as soon as possible. 
                All fields are required.
              </p>
            </div>

            <form className="contact-form__content" onSubmit={handleFullSubmit}>
              <div className="form-row">
                <div className="form-group">
                  <label htmlFor="firstName" className="form-label">First Name *</label>
                  <input
                    type="text"
                    id="firstName"
                    name="firstName"
                    className="form-input"
                    placeholder="John"
                    value={extraFields.firstName}
                    onChange={handleExtraChange}
                    required
                  />
                </div>
                <div className="form-group">
                  <label htmlFor="lastName" className="form-label">Last Name *</label>
                  <input
                    type="text"
                    id="lastName"
                    name="lastName"
                    className="form-input"
                    placeholder="Doe"
                    value={extraFields.lastName}
                    onChange={handleExtraChange}
                    required
                  />
                </div>
              </div>

              <div className="form-row">
                <div className="form-group">
                  <label htmlFor="email" className="form-label">Email *</label>
                  <input
                    type="email"
                    id="email"
                    name="email"
                    className="form-input"
                    placeholder="john@example.com"
                    value={formData.email}
                    onChange={handleChange}
                    required
                  />
                </div>
                <div className="form-group">
                  <label htmlFor="phone" className="form-label">Phone *</label>
                  <input
                    type="tel"
                    id="phone"
                    name="phone"
                    className="form-input"
                    placeholder="(208) 989-8319"
                    value={formData.phone}
                    onChange={handleChange}
                    required
                  />
                </div>
              </div>

              <div className="form-group">
                <label htmlFor="service" className="form-label">Service *</label>
                <select
                  id="service"
                  name="service"
                  className="form-select"
                  value={extraFields.service}
                  onChange={handleExtraChange}
                  required
                >
                  <option value="">Select a service</option>
                  <option value="roofing">Roofing Installation</option>
                  <option value="roof-repair">Roof Repair</option>
                  <option value="siding">Siding Installation</option>
                  <option value="siding-repair">Siding Repair</option>
                  <option value="windows">Windows</option>
                  <option value="gutters">Gutters</option>
                  <option value="full-remodel">Full Exterior Remodel</option>
                  <option value="consultation">Free Consultation</option>
                </select>
              </div>

              <div className="form-group">
                <label htmlFor="message" className="form-label">Project Details *</label>
                <textarea
                  id="message"
                  name="message"
                  className="form-textarea"
                  placeholder="Tell us about your project, timeline, and any specific requirements..."
                  value={formData.message}
                  onChange={handleChange}
                  required
                />
              </div>

              {/* ✅ unified feedback button */}
              <button
                type="submit"
                className="form-submit"
                disabled={isSubmitting || submitSuccess}
              >
                {isSubmitting ? (
                  <>
                    <span
                      className="spinner"
                      style={{
                        display: 'inline-block',
                        width: '14px',
                        height: '14px',
                        border: '2px solid #fff',
                        borderTopColor: 'transparent',
                        borderRadius: '50%',
                        marginRight: '8px',
                        animation: 'spin 1s linear infinite'
                      }}
                    />
                    Sending...
                  </>
                ) : submitSuccess ? (
                  '✅ Thank you!'
                ) : (
                  <>
                    <FaPaperPlane />
                    <span>Send Message</span>
                  </>
                )}
              </button>
            </form>
          </div>
        </div>
      </div>

      {/* spinner animation keyframes */}
      <style>{`
        @keyframes spin {
          from { transform: rotate(0deg); }
          to { transform: rotate(360deg); }
        }
      `}</style>
    </div>
  );
};

export default ContactPage;
